body {
  background-color: #39a069;
  width: 100%;
  min-height: 100vh;
  margin: 0px;
  padding: 0px;
  direction: rtl;
  padding: 25px;
}
h1{
  font-size: 60px;

}
.center{
  text-align: center;
  color: #f2f8fa;
}
.logo{
  width: 15%;
  height: auto;
  margin: 0px auto;
}
.timeparent {
  display: flex;
  justify-content: center;
  align-items: center;
}
.time {
  color: white;
  font-size: 70px;
  position: relative;
  display: block;
  margin-top: 45px;
  color: rgb(239, 248, 251);
}
.timecontainer {
  text-align: center;
  height: 175px;
  width: 400px;
  border: 10px solid rgb(239, 248, 251);
  border-radius: 20px;
}
.hidden{
  display: none;
}

@media only screen and (max-width: 600px) {
  .timecontainer {
    margin-left: 10px;
    margin-right: 10px;
  }
}
table{
  margin: 20px auto;
  min-width: 30%;
}
td,th{
  border: 1px solid rgb(238, 165, 28);
}s
th,td{
  padding: 2px;
}
h3{
  font-weight: bold;
}
.orng{
  color: orange
}
.wht{
  color: rgb(180, 53, 7);
  font-weight: bold;
}